import data from './data';
import layout from './layout';
import projectStore from '@/store';
import constants from '../data/constants';
import api from 'Api';
import Vue from 'vue';
import _ from 'lodash';

const store = {
  namespaced: true,
  modules: {
    data,
    layout
  },
  state: {
    markdown: {
      fileId: 0,
      text: '\n',
      // problemText: '\n',
      // evaluationText: '\n',
      // dataText: '\n',
      properties: '\n',
      selectionStart: 0,
      selectionEnd: 0,
      scrollPosition: null
    },
    tips: {
      id: '',
      title: '',
      html: '',
      updatedAt: ''
    }
  },
  getters: {
    getTipsOpen: function(state) {
      return state.tips.tipsOpen;
    },
    getTips: function(state) {
      return state.tips;
    }
  },
  mutations: {
    patchMarkdown: (state, value) => {
      let _value = _.pick(value, [
        'fileId',
        'text',
        'properties',
        // 'problemText',
        // 'evaluationText',
        // 'dataText',
        'selectionStart',
        'selectionEnd',
        'scrollPosition'
      ]);
      _.forOwn(_value, (v, k) => (state.markdown[k] = v));
      // if (_value.fileId === 'Question-markdown') {
      //   state.markdown.problemText = _value.text;
      // }
      // if (_value.fileId === 'Evaluation-markdown') {
      //   state.markdown.evaluationText = _value.text;
      // }
      // if (_value.fileId === 'data-markdown') {
      //   state.markdown.dataText = _value.text;
      // }
    },
    changeTipsId: (state, id) => {
      let project = projectStore.getters['projectDetail/project'];
      let ideasList = project.ideas;
      ideasList.forEach(idea => {
        if (idea.code === id) {
          state.tips = {
            code: idea.code,
            title: idea.title,
            html: idea.content.previewHtml,
            updatedAt: idea.updatedAt
          };
        }
      });
    }
  },
  actions: {}
};

export default store;
