import extensionSvc from '../services/extensionSvc';
import store from '@/store';

let ideaHashCodeReg = /#\d+/gi;
function render_idea_hashcode(tokens, idx) {
  let projectId = store.getters['projectDetail/project'].id;
  let ideas = store.getters['projectDetail/project'].ideas;
  let hashCode = tokens[idx].content;
  let ideaCode = /(\d+)/.exec(hashCode)[1];
  let itemIdea = ideas.find(x => x.code === parseInt(ideaCode));
  if (itemIdea) {
    let ideaId = itemIdea.id;
    return `<a class='ml-project-idea-hash-code' target='_blank' href='/projects/${projectId}/ideas/${ideaId}'>${hashCode}</a>`;
  } else {
    return `#${ideaCode}`;
  }
}

function createIdeaHashCodeRule(md) {
  var arrayReplaceAt = md.utils.arrayReplaceAt;
  var ucm = md.utils.lib.ucmicro;
  var ZPCc = new RegExp([ucm.Z.source, ucm.P.source, ucm.Cc.source].join('|'));

  function splitTextToken(text, level, Token) {
    var token;
    var last_pos = 0;
    var nodes = [];
    text.replace(ideaHashCodeReg, function(ideaHashCode, offset, src) {
      // Don't allow letters before any hashCode
      if (offset > 0 && !ZPCc.test(src[offset - 1])) {
        return;
      }

      // Don't allow letters after any hashCode
      if (
        offset + ideaHashCode.length < src.length &&
        !ZPCc.test(src[offset + ideaHashCode.length])
      ) {
        return;
      }

      if (offset > last_pos) {
        token = new Token('text', '', 0);
        token.content = text.slice(last_pos, offset);
        nodes.push(token);
      }

      token = new Token('ideaHashCode', 'a', 0);
      token.markup = ideaHashCode;
      token.content = ideaHashCode;
      nodes.push(token);

      last_pos = offset + ideaHashCode.length;
    });

    if (last_pos < text.length) {
      token = new Token('text', '', 0);
      token.content = text.slice(last_pos);
      nodes.push(token);
    }

    return nodes;
  }

  return function idea_hashcode_rule(state) {
    var i;
    var j;
    var l;
    var tokens;
    var token;
    var blockTokens = state.tokens;
    var autolinkLevel = 0;

    for (j = 0, l = blockTokens.length; j < l; j++) {
      if (blockTokens[j].type !== 'inline') {
        continue;
      }
      tokens = blockTokens[j].children;

      // We scan from the end, to keep position when new tags added.
      // Use reversed logic in links start/end match
      for (i = tokens.length - 1; i >= 0; i--) {
        token = tokens[i];

        if (token.type === 'link_open' || token.type === 'link_close') {
          if (token.info === 'auto') {
            autolinkLevel -= token.nesting;
          }
        }

        if (
          token.type === 'text' &&
          autolinkLevel === 0 &&
          ideaHashCodeReg.test(token.content)
        ) {
          // replace current node
          blockTokens[j].children = tokens = arrayReplaceAt(
            tokens,
            i,
            splitTextToken(token.content, token.level, state.Token)
          );
        }
      }
    }
  };
}

extensionSvc.onGetOptions((options, properties) => {
  options.ideaHashCode = properties.extensions.ideaHashCode.enabled;
});

extensionSvc.onInitConverter(3, (markdown, options) => {
  if (options.ideaHashCode) {
    markdown.use(function(md) {
      let rule = createIdeaHashCodeRule(md);
      md.renderer.rules.ideaHashCode = render_idea_hashcode;
      md.core.ruler.push('ideaHashCode', rule);
    });
  }
});
