import DiffMatchPatch from 'diff-match-patch';
import cledit from './cledit';
import utils from '../utils';
import store from '@/store';

let clEditor;
let previousPatchableText;
let currentPatchableText;
let isChangePatch;

const diffMatchPatch = new DiffMatchPatch();

function makePatches() {
  const diffs = diffMatchPatch.diff_main(
    previousPatchableText,
    currentPatchableText
  );
  return diffMatchPatch.patch_make(previousPatchableText, diffs);
}

function applyPatches(patches) {
  const newPatchableText = diffMatchPatch.patch_apply(
    patches,
    currentPatchableText
  )[0];
  let result = newPatchableText;

  // Expect a `contentChanged` event
  if (result !== clEditor.getContent()) {
    previousPatchableText = currentPatchableText;
    currentPatchableText = newPatchableText;
    isChangePatch = true;
  }
  return result;
}

function reversePatches(patches) {
  const result = diffMatchPatch.patch_deepCopy(patches).reverse();
  result.forEach(patch => {
    patch.diffs.forEach(diff => {
      diff[0] = -diff[0];
    });
  });
  return result;
}

export default {
  createClEditor(editorElt) {
    this.clEditor = cledit(editorElt, editorElt.parentNode, true);
    ({ clEditor } = this);
    clEditor.on('contentChanged', text => {
      const oldContent = store.state.stackEdit.markdown;
      const newContent = {
        ...utils.deepCopy(oldContent),
        text: utils.sanitizeText(text)
      };
      if (!isChangePatch) {
        previousPatchableText = currentPatchableText;
        currentPatchableText = newContent.text;
      } else {
        newContent.text = currentPatchableText;
      }
      store.commit('stackEdit/patchMarkdown', newContent);
      isChangePatch = false;
    });
  },
  initClEditorInternal(opts) {
    const markdown = store.state.stackEdit.markdown;
    const options = Object.assign(
      {
        selectionStart: markdown.selectionStart,
        selectionEnd: markdown.selectionEnd,
        patchHandler: {
          makePatches,
          applyPatches,
          reversePatches
        }
      },
      opts
    );

    options.content = markdown.text;

    if (currentPatchableText !== previousPatchableText) {
      currentPatchableText = markdown.text;
      previousPatchableText = currentPatchableText;
    }

    clEditor.init(options);
  },
  getTrimmedSelection() {
    const { selectionMgr } = clEditor;
    let start = Math.min(
      selectionMgr.selectionStart,
      selectionMgr.selectionEnd
    );
    let end = Math.max(selectionMgr.selectionStart, selectionMgr.selectionEnd);
    const text = clEditor.getContent();
    while ((text[start] || '').match(/\s/)) {
      start += 1;
    }
    while ((text[end - 1] || '').match(/\s/)) {
      end -= 1;
    }
    return start < end && { start, end };
  }
};
