<template>
  <div
    class="stack-edit-container layout flex flex--column"
    :class="stackEditContainerClasses"
  >
    <div
      class="layout__panel layout__panel--navigation-bar"
      v-show="styles.showNavigationBar"
      :style="{ height: constants.navigationBarHeight + 'px' }"
    >
      <navigation-bar
        @stackEditShowContentFun="stackEditShowContentFun"
      ></navigation-bar>
    </div>
    <div class="layout__panel flex flex--row layout-content-box">
      <div
        class="layout__panel layout__panel--editor stackDropBox content-height"
        @drop="drop"
        @dragover.prevent
        v-show="stackEditShowContent.showEditor"
        :style="{ fontSize: styles.fontSize + 'px' }"
      >
        <editor></editor>

        <div class="progress-bar-edit">
          <!-- <input
            accept=".jpeg,.jpg,.png,.gif,.svg"
            type="file"
            @change="getFile($event, 'user_images')"
            multiple="multiple"
            class="file-btn"
          /> -->
          <div class="progress-bar-edit-item">
            <div class="error-default" v-if="!loadingShow">
              {{ autoSaveMessages }}
            </div>
            <div class="loading-box" v-else>
              <div class="loading-ico">
                <img src="/img/loading.gif" />
              </div>
              <div>{{ uploadText }}</div>
            </div>
          </div>
        </div>
      </div>

      <div
        class="layout__panel layout__panel--preview preview-width content-height new-privew-content"
        v-show="stackEditShowContent.showPreview"
        :style="{ fontSize: styles.fontSize + 'px' }"
      >
        <preview></preview>
      </div>
    </div>
  </div>
</template>

<script>
import '../styles/markdownHighlighting.scss';
import '../styles/app.scss';
import { mapState, mapGetters, mapMutations } from 'vuex';
import NavigationBar from './NavigationBar';
import StatusBar from './StatusBar';
import Editor from './Editor';
import Preview from './Preview';
import editorSvc from '../services/editorSvc';
import markdownConversionSvc from '../services/markdownConversionSvc';
import store from '@/store';
import ossUploadFiles from '../../../oss';
export default {
  name: 'SatckEditLayout',
  data() {
    return {
      loadingShow: false,
      uploadText: '',
      stackEditShowContent: {
        showEditor: true,
        showPreview: false
      },
      trans: {
        ErrorImageFormat: this.$i18n.t('StackEdit.Layout.ErrorImageFormat'),
        ErrorImageSize: this.$i18n.t('StackEdit.Layout.ErrorImageSize'),
        ErrorUpdatedImage: this.$i18n.t('StackEdit.Layout.ErrorUpdatedImage'),
        ErrorDefault: this.$i18n.t('StackEdit.Layout.ErrorDefault'),
        ErrorPictures: this.$i18n.t('StackEdit.Layout.ErrorPictures'),
        ErrorUpload: this.$i18n.t('StackEdit.Layout.ErrorUpload'),
        UploadedSuccessfully: this.$i18n.t(
          'StackEdit.Layout.UploadedSuccessfully'
        ),
        OTAllChangesSaved: this.$i18n.t('StackEdit.Layout.OTAllChangesSaved')
      }
    };
  },
  components: {
    NavigationBar,
    StatusBar,
    Editor,
    Preview
  },
  computed: {
    ...mapGetters('stackEdit/layout', ['constants', 'styles']),
    ...mapGetters('stackEdit/data', ['layoutSettings']),
    ...mapGetters({
      autoSaveMessages: 'projectCollection/autoSaveMessagesGetters'
    }),
    stackEditContainerClasses() {
      const themeClasses = {
        light: ['app--light'],
        dark: ['app--dark']
      };
      const result =
        themeClasses[
          store.getters['stackEdit/data/computedSettings'].colorTheme
        ];
      let classes = Array.isArray(result) ? result : themeClasses.light;
      if (this.styles.expanded) {
        classes.push('stack-edit-plus-active-nav');
      }
      return classes;
    }
  },
  mounted() {},
  methods: {
    updateLayoutContainerSize() {},
    saveSelection: () => editorSvc.saveSelection(true),
    stackEditShowContentFun(e) {
      this.stackEditShowContent.showEditor = e.showEditor;
      this.stackEditShowContent.showPreview = e.showPreview;
      // 表示stackEdit预览中的超参和结果
      if (e.showPreview) {
        let pParameterHtmlList = $('.preview pre.language-parameter').parent(
          '.cl-preview-section'
        );
        let pResultHtmlList = $('.preview pre.language-result').parent(
          '.cl-preview-section'
        );
        let _parameterHtml = '<span class="parameter-tag mdi mdi-tune tag-ps" title="parameter"></span>';
        if (!$('.preview .parameter-tag').length) {
          for (let i = 0; i < pParameterHtmlList.length; i++) {
            $(pParameterHtmlList[i]).append(_parameterHtml);
          }
        }

        let _resultHtml = '<span class="result-tag mdi mdi-subtitles-outline tag-ps" title="result"></span>';
        if (!$('.preview .result-tag').length) {
          for (let i = 0; i < pResultHtmlList.length; i++) {
            $(pResultHtmlList[i]).append(_resultHtml);
          }
        }
      }
    },
    async uploadFiles(files, category) {
      //Limit the number of uploads to 5
      if (files.length > 5) {
        this.$store.commit(
          'projectCollection/autoSaveMessagesFun',
          this.trans.ErrorPictures
        );
        return;
      }
      //Restrict upload formats to jpeg, png, jpg, gif, svg, size no more than 1M
      for (let i = 0; i < files.length; i++) {
        let imageType = [
          'image/jpeg',
          'image/png',
          'image/jpg',
          'image/gif',
          'image/svg+xml'
        ];
        if (!imageType.includes(files[i].type)) {
          this.$store.commit(
            'projectCollection/autoSaveMessagesFun',
            this.trans.ErrorImageFormat
          );
          return;
        }
        if (files[i].size > 1048576) {
          this.$store.commit(
            'projectCollection/autoSaveMessagesFun',
            this.trans.ErrorImageSize
          );
          return;
        }
      }
      this.loadingShow = true;
      $('.file-btn1').attr('disabled', true);
      this.uploadText = this.trans.ErrorUpdatedImage;
      let imagesArry = [];
      try {
        imagesArry = await ossUploadFiles(files, category);
      } catch (error) {
        store.commit('settings/showGlobalSnackbar', {
          text: this.trans.ErrorUpload
        });
        this.loadingShow = false;
        this.$store.commit(
          'projectCollection/autoSaveMessagesFun',
          this.trans.OTAllChangesSaved
        );
      }
      $('.file-btn1').val('');
      $('.file-btn1').attr('disabled', false);
      for (let j = 0; j < imagesArry.length; j++) {
        let links = imagesArry[imagesArry.length - j - 1].map(
          x => `![${x.name}](${x.href})`
        );
        let linkContent = `\n${links.join('\n')}`;
        let linkContentNode = document.createTextNode(linkContent);
        // editorSvc.selectionRange.deleteContents();
        editorSvc.selectionRange.insertNode(linkContentNode);
        this.loadingShow = false;
        this.$store.commit(
          'projectCollection/autoSaveMessagesFun',
          this.trans.UploadedSuccessfully
        );
      }
    },
    drop(event) {
      event.preventDefault();
      let files = event.dataTransfer.files;
      this.uploadFiles(files);
    },
    getFile(event, category) {
      let files = event.target.files;
      this.uploadFiles(files, category);
    }
  },
  async created() {
    try {
      markdownConversionSvc.init(); // Needs to be inited before mount
      window.addEventListener('resize', this.updateLayoutContainerSize);
      window.addEventListener('keyup', this.saveSelection);
      window.addEventListener('mouseup', this.saveSelection);
      window.addEventListener('focusin', this.saveSelection);
      window.addEventListener('contextmenu', this.saveSelection);
    } catch (err) {
      console.error(err);
    }
  },
  mounted() {
    const editorElt = this.$el.querySelector('.editor__inner');
    const previewElt = this.$el.querySelector('.preview__inner-2');
    editorSvc.init(editorElt, previewElt);

    // Focus on the editor every time reader mode is disabled
    const focus = () => {
      if (this.styles.showEditor) {
        editorSvc.clEditor.focus();
      }
    };
    setTimeout(focus, 100);
    this.updateLayoutContainerSize();
    this.$watch(() => this.styles.showEditor, focus);
  },
  destroyed() {
    window.removeEventListener('resize', this.updateLayoutContainerSize);
    window.removeEventListener('keyup', this.saveSelection);
    window.removeEventListener('mouseup', this.saveSelection);
    window.removeEventListener('focusin', this.saveSelection);
    window.removeEventListener('contextmenu', this.saveSelection);
  }
};
</script>

