<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import editorSvc from '../services/editorSvc';
import animationSvc from '../services/animationSvc';
import utils from '../services/utils';
import pagedownButtons from '../data/pagedownButtons';
import store from '@/store';

// According to mousetrap
const mod = /Mac|iPod|iPhone|iPad/.test(navigator.platform) ? 'Meta' : 'Ctrl';

const getShortcut = method => {
  let result = '';
  Object.entries(
    store.getters['stackEdit/data/computedSettings'].shortcuts
  ).some(([keys, shortcut]) => {
    if (`${shortcut.method || shortcut}` === method) {
      result = keys
        .split('+')
        .map(key => key.toLowerCase())
        .map(key => {
          if (key === 'mod') {
            return mod;
          }
          // Capitalize
          return key && `${key[0].toUpperCase()}${key.slice(1)}`;
        })
        .join('+');
    }
    return result;
  });
  return result && ` – ${result}`;
};

export default {
  props: {
    expandAllActive: {
      type: Boolean
    }
  },
  data: () => ({
    mounted: false,
    state: 'edit'
  }),
  computed: {
    ...mapState('stackEdit/layout', ['canUndo', 'canRedo']),
    ...mapGetters('stackEdit/layout', ['styles']),
    pagedownButtons() {
      return pagedownButtons.map(button => ({
        ...button,
        titleWithShortcut: `${button.title}${getShortcut(button.method)}`,
        iconClass: `icon-${button.icon}`
      }));
    }
  },
  methods: {
    undo() {
      return editorSvc.clEditor.undoMgr.undo();
    },
    redo() {
      return editorSvc.clEditor.undoMgr.redo();
    },
    getFile(event, category) {
      this.$parent.getFile(event, category);
    },
    getParameterAndResult(name) {
      let insertContent = '';
      if (name === 'parameter') {
        insertContent = `\`\`\`parameter\n\`\`\`\n`;
      } else {
        insertContent = `\`\`\`result\n\`\`\`\n`;
      }
      let insertContentNode = document.createTextNode(insertContent);
      // editorSvc.selectionRange.deleteContents();
      editorSvc.selectionRange.insertNode(insertContentNode);
    },
    pagedownClick(name) {
      editorSvc.pagedownEditor.uiManager.doClick(name);
    },
    toggleState(state) {
      switch (state) {
        case 'preview':
          this.$emit('stackEditShowContentFun', {
            showEditor: false,
            showPreview: true
          });
          break;
        case 'sidePreview':
          this.$emit('stackEditShowContentFun', {
            showEditor: true,
            showPreview: true
          });
          break;
        case 'edit':
        default:
          this.$emit('stackEditShowContentFun', {
            showEditor: true,
            showPreview: false
          });
          break;
      }
    },
    collapseAll() {
      // Exit Full Screen
      this.toggleExpanded(false);
      this.$emit('stackEditShowContentFun', {
        showEditor: true,
        showPreview: false
      });
    },
    expandAll() {
      // Full screen display
      this.toggleExpanded(true);
    },
    ...mapActions('stackEdit/data', [
      'toggleExpanded',
      'toggleEditor',
      'toggleSidePreview',
      'toggleStatusBar'
    ])
  },
  created() {},
  mounted() {
    this.mounted = true;
  }
};
</script>

