import './extensions';
import './services/optional';
import './icons';
import './components/common/vueGlobals';
import stackEditEditor from './components/Layout.vue';
import store from '@/store';

if (!window[Symbol('stackEditEditorImported')]) {
  $(document)
    .on('mouseenter', '.ml-project-idea-hash-code', function(event) {
      let topStyle = event.target.getBoundingClientRect().top - 10;
      let leftStyle =
        event.target.getBoundingClientRect().left -
        event.target.offsetWidth / 2 -
        15;
      if (event.clientY < 150) {
        $('.ideas-tips-mouseover')
          .css({
            top: topStyle + 38,
            bottom: '',
            left: leftStyle
          })
          .addClass('bottom-active');
      } else {
        $('.ideas-tips-mouseover')
          .css({
            top: '',
            bottom: 'calc(100vh - ' + topStyle + 'px)',
            left: leftStyle
          })
          .removeClass('bottom-active');
      }
      $('.ideas-tips-mouseover').show(0);
      let tipsId = parseInt(event.target.innerText.replace('#', ''));
      store.commit('stackEdit/changeTipsId', tipsId);
    })
    .on('mouseleave', '.ml-project-idea-hash-code', function(event) {
      $('.ideas-tips-mouseover')
        .delay(200)
        .hide(0);
    });

  $(document)
    .on('mouseenter', '.ideas-tips-mouseover', function(event) {
      $('.ideas-tips-mouseover').show(0);
    })
    .on('mouseleave', '.ideas-tips-mouseover', function(event) {
      $('.ideas-tips-mouseover').hide(0);
    });
  window[Symbol('stackEditEditorImported')] = true;
} else {
  console.warn('stack edit editor imported multi times ');
}

export default stackEditEditor;
